import React from "react";
import { BrowserRouter, Route } from "react-router-dom";
import "./css/reset.css";
import Home from "./routes/Home";
import Detail from "./routes/Detail";

class App extends React.Component {
	render() {
		return (
			<BrowserRouter>
				<Route path="/movie_app_2020/" exact={true} component={Home} />
				<Route path="/movie_app_2020/:name" component={Detail} render={(routeProps) => console.log(routeProps)} />
			</BrowserRouter>
		);
	}
}

export default App;
